export interface ICreateProjectRequest {
  project_name: string;
  customer_id: string;
  contractor_id: string;
  project_type: string;
  project_member_id: string;
  construction_start?: string;
  construction_stop?: string;
  construction_scale?: string;
  budget?: string;
  project_postal_code: string;
  project_province: string;
  project_address: string;
  total_man_works?: string;
  contract_type: string;
  construction_types: string[];
  materials: string[];
  description?: string;
  image_url?: string;
  project_work_status_name?: string;
}

export interface IMember {
  project_bind_id: string;
  project_id: string;
  project_member_id: string;
}

export interface IProjectAddress {
  project_address_id: string;
  project_id: string;
  project_postal_code: string;
  project_province: string;
  project_address: string;
}

export interface IProjectContractStatus {
  project_contract_status_id: string;
  is_project_stop: boolean;
  project_id: string;
  status_name: string;
  status_reason: string;
  status_timestamp: string;
}

export interface IProjectWorkStatus {
  project_work_status_id: string;
  is_project_stop: boolean;
  project_id: string;
  status_name: string;
  status_reason: string;
  status_timestamp: string;
}

export interface IProjectResponse {
  project_id: string;
  project_name: string;
  customer_id: string;
  contractor_id: string;
  project_type: string;
  project_member: IMember;
  construction_start: Date;
  construction_stop: Date;
  construction_scale?: string;
  budget?: string;
  address?: IProjectAddress;
  total_man_works?: string;
  contract_type: string;
  construction_types: string[];
  materials: string[];
  image_url?: string;
  description?: string;
  contract_status: IProjectContractStatus;
  work_status: IProjectWorkStatus;
  created_user_id: string;
}

export interface IUpdateProjectRequest extends Partial<ICreateProjectRequest> {
  project_work_status_name?: string;
  project_work_status_reason?: string;
  project_contract_status_name?: string;
  project_contract_status_reason?: string;
}
